.navbar {
  background-color: transparent;
  color: #ffffff;
  padding: 12px 26px 14px 26px !important;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.nav-logo {
  color: white;
  font-weight: 800;

  font-size: 30px;
  margin-right: 30px;
}

.bars {
  font-size: 20px;
  background: none;
  color: #01203a;
}

.bars:hover {
  color: #e89c35;
}

.nav-menu {
  background-color: #01203a;
  width: 225px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 1500ms;
  filter: drop-shadow(0px 0px 8px #000000);
  z-index: 1001;
}

.nav-menu.active {
  left: 0;
  transition: 600ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 2px 11px 2px 11px;
  list-style: none;
  height: 50px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 15px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 15px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #364552;
  text-decoration: none;
  color: #e89c35;
  border-radius: 35px;
}

.nav-menu-items {
  width: 100%;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}

.navbar-toggle {
  background-color: #01203a;
  width: 100%;
  padding: 16px 16px 10px 23px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}

@media screen and (max-width: 480px) {
  .navbar {
    padding: 8px 14px 14px 14px !important;
  }

  .nav-text {
    padding: 2px 7px 2px 7px;
  }

  .navbar-toggle {
    padding: 12px 12px 10px 19px;
  }
}
